'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import styles from './page.module.scss'; // Ensure this matches the file name

export default function LoginPage() {
    const [password, setPassword] = useState('');
    const [error, setError] = useState(false);
    const router = useRouter();

    useEffect(() => {
        // If already authenticated, redirect to home
        const sessionAuth = sessionStorage.getItem('is_authenticated');
        if (sessionAuth === 'true') {
            router.push('/');
        }
    }, [router]);

    const handleLogin = (e: React.FormEvent) => {
        e.preventDefault();
        if (password === 'louis_bewerbung') {
            sessionStorage.setItem('is_authenticated', 'true');
            setError(false);
            router.push('/');
        } else {
            setError(true);
            setPassword('');
        }
    };

    return (
        <div className={styles.loginContainer}>
            <div className={styles.loginCard}>
                <h1>Willkommen</h1>
                <p>Bitte geben Sie das Zugangspasswort ein.</p>

                <form onSubmit={handleLogin} className={styles.form}>
                    <input
                        type="password"
                        placeholder="Passwort"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        className={error ? styles.inputError : ''}
                    />
                    {error && <span className={styles.errorMessage}>Falsches Passwort</span>}
                    <button type="submit">Eintreten</button>
                </form>
            </div>
        </div>
    );
}
